/* Gallery Block Styles */

.eara-gallery {
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.eara-gallery__grid {
  width: 100%;
}

.eara-gallery__link {
  text-decoration: none;
  display: block;
  color: inherit;
}

.eara-gallery__item {
  position: relative;
  overflow: hidden;
  border-radius: 4px;
}

.eara-gallery__image-wrapper {
  position: relative;
  width: 100%;
  overflow: hidden;
  background-color: #f5f5f5;
  transition: transform 0.3s ease;

  &:hover {
    transform: scale(1.02);
  }
}

.eara-gallery__link:hover .eara-gallery__image-wrapper {
  transform: scale(1.05);
  cursor: pointer;
}

.eara-gallery__image {
  width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
  transition: opacity 0.3s ease;
}

.eara-gallery__link:hover .eara-gallery__image {
  opacity: 0.9;
}

/* Grid Layout */
.eara-gallery--grid {
  .eara-gallery__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 16px;
  }

  .eara-gallery__image-wrapper {
    aspect-ratio: 1 / 1;
  }

  .eara-gallery__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

/* Masonry Layout */
.eara-gallery--masonry {
  .eara-gallery__grid {
    column-count: 3;
    column-gap: 16px;
  }

  .eara-gallery__item {
    break-inside: avoid;
    margin-bottom: 16px;
    display: inline-block;
    width: 100%;
  }

  .eara-gallery__image {
    width: 100%;
    height: auto;
  }
}

/* Responsive adjustments */
@media (max-width: 1024px) {
  .eara-gallery--grid .eara-gallery__grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .eara-gallery--masonry .eara-gallery__grid {
    column-count: 2;
  }
}

@media (max-width: 640px) {
  .eara-gallery--grid .eara-gallery__grid {
    grid-template-columns: 1fr;
    gap: 12px;
  }

  .eara-gallery--masonry .eara-gallery__grid {
    column-count: 1;
    column-gap: 0;
  }

  .eara-gallery--masonry .eara-gallery__item {
    margin-bottom: 12px;
  }

  .eara-gallery__image-wrapper:hover {
    transform: none;
  }

  .eara-gallery__link:hover .eara-gallery__image-wrapper {
    transform: none;
  }
}

/* Loading state */
.eara-gallery__image {
  animation: fadeIn 0.5s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Optional: Add lightbox effect for linked images */
.eara-gallery__link {
  position: relative;

  &::after {
    content: "ðŸ”—";
    position: absolute;
    top: 8px;
    right: 8px;
    background-color: rgba(255, 255, 255, 0.9);
